html {
  min-width: 240px;
  background-color: #fff;
  font-family: 'Myriad Pro', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media all and (max-width: 639px) {
  html {
    font-size: 14px;
  }
}

p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h4 {
  font-size: 3rem;
  font-weight: unset;
  margin: 0 0 0.75rem;
}

h1 {
  margin-bottom: 2.5rem;
}

img {
  max-width: 100%;
}

.wrapper {
  overflow: hidden;
}

.container {
  max-width: 976px;
  margin: 0 auto;
  padding: 6rem 12px;
}

@media all and (max-width: 767px) {
  .container {
    padding: 2rem 12px;
  }
}

.title {
  text-align: center;
  letter-spacing: -1.1px;
  line-height: 1;
}

.title__caption {
  text-align: center;
  font-size: 1.5rem;
  line-height: 2rem;
  padding-bottom: 2rem;
}

@media all and (min-width: 768px) {
  .title__caption {
    padding-bottom: 4rem;
  }
}

.melon {
  display: block;
  margin: 0 auto;
}

.up {
  display: block;
  height: 48px;
  width: 48px;
  border-radius: 50%;
  background-color: #71ae5a;
  position: fixed;
  right: 12px;
  bottom: 12px;
  font-size: 14px;
}

.up::before {
  content: '';
  font-size: 0;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 15px solid #e9ede9;
  position: relative;
  left: 15px;
}

.up:hover {
  background-color: #6BC04B;
}

.header {
  color: #fff;
  background-color: #528354;
  background: radial-gradient(738.5px at 50% 100%, #528354 0%, rgba(71, 168, 92, 0.3) 70%, rgba(51, 87, 53, 0.15) 100%), #335735;
  padding-top: 12px;
}

@media all and (min-width: 768px) {
  .header {
    padding-top: 2.5rem;
  }
}

.top-bar {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 12px;
  margin: 0 auto;
  max-width: 976px;
}

.logo {
  font-family: 'Myriad Pro - Bold', 'Myriad Pro', sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 40px;
  letter-spacing: -2px;
  padding: 0 8px;
  margin-left: 56px;
  position: relative;
}

.logo::before {
  content: '';
  display: inline-block;
  position: absolute;
  top: 0;
  left: -56px;
  width: 56px;
  height: 1em;
  background-image: url("../img/logo.png");
  background-image: url("../img/logo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.nav {
  font-family: 'Myriad Pro - Semibold', 'Myriad Pro', sans-serif;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.65;
  letter-spacing: -1px;
}

@media all and (min-width: 768px) {
  .nav {
    display: flex;
    flex-flow: row;
    justify-content: flex-end;
  }
}

@media all and (max-width: 767px) {
  .nav {
    visibility: hidden;
    position: absolute;
    top: 2em;
    left: 0;
    right: 0;
    z-index: 500;
    background-color: #528354f2;
  }
  .nav-state:checked ~ .nav {
    visibility: visible;
  }
}

.nav__item {
  padding: 0 16px;
  box-sizing: border-box;
  border: 2px solid #fff0;
}

@media all and (min-width: 768px) {
  .nav__item {
    margin-right: 0.5em;
  }
  .nav__item:last-child {
    margin-right: 0;
  }
  .nav__item:hover {
    border-color: #fff;
    transition: border-color 0.4s cubic-bezier(0, 0.7, 0.5, 1.15);
  }
}

@media all and (max-width: 767px) {
  .nav__item {
    display: block;
    text-align: right;
  }
}

/*  Menu Icon  */
.menu-icon {
  position: relative;
  width: 40px;
  height: 40px;
  display: inline-block;
  align-self: center;
  cursor: pointer;
}

@media all and (min-width: 768px) {
  .menu-icon {
    display: none;
  }
}

.menu-icon span {
  position: absolute;
  left: 5px;
  right: 5px;
  height: 2.5px;
  width: 75%;
  background-color: #fff;
  transform: rotate(0deg);
  opacity: 1;
}

.menu-icon span:nth-child(1) {
  bottom: 30px;
}

.menu-icon span:nth-child(2) {
  bottom: 20px;
}

.menu-icon span:nth-child(3) {
  bottom: 10px;
}

.menu-icon:hover span,
.menu-icon:focus span,
.menu-icon:active span {
  background: #eee;
  transition: all 0.2s cubic-bezier(0.3, 0.3, 0.7, 0.7);
}

.menu-state {
  display: none;
}

.menu-state:checked ~ .menu-icon span:nth-child(1),
.menu-state:checked ~ .menu-icon span:nth-child(3) {
  top: 20px;
  transform: translate(0, -50%);
  opacity: 0.9;
}

.menu-state:checked ~ .menu-icon span:nth-child(1) {
  transform: rotate(45deg);
}

.menu-state:checked ~ .menu-icon span:nth-child(3) {
  transform: rotate(-45deg);
}

.menu-state:checked ~ .menu-icon span:nth-child(2) {
  opacity: 0;
}

