    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

.components__item {
  color: #fff;
  font-size: 18px;
  line-height: 1.5;
  display: list-item;
  list-style: none;
  /*    Базовые отступы    */
}

.components__item span {
  padding-left: 8px;
  padding-right: 8px;
}

@media all and (max-width: 480px) {
  .components__item {
    padding-left: 32px;
    background: url(../img/shape.png) no-repeat 0 50%;
    background-image: url(../img/shape.svg);
  }
}

@media all and (min-width: 481px) {
  .components__item {
    display: inline-block;
    position: absolute;
    /*    Круги    */
    /*    Подчеркивание   */
    /*    Protein    */
    /*    Calories    */
    /*    Water    */
    /*    Fat    */
  }
  .components__item::before {
    content: '';
    display: inline-block;
    height: 24px;
    width: 24px;
    background: url(../img/shape.svg) center center no-repeat;
    background-size: contain;
    position: absolute;
    z-index: 10;
  }
  .components__item::after {
    content: '';
    display: block;
    border-top: 3px solid;
    position: relative;
    bottom: 3px;
  }
  .components__item:nth-child(1) {
    top: 0;
    left: 50%;
    margin-left: 104px;
  }
  .components__item:nth-child(1)::before {
    bottom: -20px;
    left: -48px;
  }
  .components__item:nth-child(1)::after {
    height: 32px;
    border-left: 4px solid;
    border-color: #d4fa7a;
    transform: skewX(-45deg);
    transform-origin: 0 0;
  }
  .components__item:nth-child(2) {
    top: 49%;
    right: 50%;
    margin-right: 96px;
  }
  .components__item:nth-child(2)::before {
    top: -10px;
    right: -38px;
  }
  .components__item:nth-child(2)::after {
    height: 8px;
    border-right: 8px solid;
    border-color: #fd6363;
    transform: rotateX(180deg) skewX(70deg);
    transform-origin: 0 0;
  }
  .components__item:nth-child(3) {
    top: 54%;
    left: 50%;
    margin-left: 82px;
  }
  .components__item:nth-child(3)::before {
    top: 13px;
    left: -24px;
  }
  .components__item:nth-child(3)::after {
    border-color: #8cdbf2;
  }
  .components__item:nth-child(3) span {
    padding-left: 56px;
  }
  .components__item:nth-child(4) {
    top: 82%;
    right: 50%;
    margin-right: 16px;
  }
  .components__item:nth-child(4)::before {
    top: 13px;
    right: -24px;
  }
  .components__item:nth-child(4)::after {
    border-color: #fef45a;
  }
  .components__item:nth-child(4) span {
    padding-right: 96px;
  }
}

